import imageURL from "./images/gazetry3.png";
import "pannellum";

function init() {
  const panoViewer = pannellum.viewer("mypanorama", {
    type: "equirectangular",
    panorama: imageURL,
    autoLoad: true,
    showFullscreenCtrl: true,
    showZoomCtrl: false,
    pitch: -36,
    hfov: 80
  });

  panoViewer.on("load", function () {
    document.querySelector(".panorama").classList.add("is-loaded");
    document.querySelector(".loading").classList.add("is-loaded-txt");
    //image loaded, start transition
  });

}

document.addEventListener("DOMContentLoaded", init());
